<template>
  <div class="home">
    <Header />
    <!-- <mt-header fixed title="固定在顶部"></mt-header> -->
    <div class="body">
      <div class="nav color">
        <div
          class="item"
          v-for="(item, index) in list_first"
          :key="index"
          @click="skipto(item)"
        >
          <span class="font icon" :class="item.icon"></span>
          <span class="title">{{ item.title }}</span>
        </div>
      </div>
      <div class="nav nav_second">
        <div
          class="item_second"
          v-for="(item, index) in list_second"
          :key="index"
          @click="skipto(item)"
        >
          <span
            class="font icon_second"
            :class="item.icon"
            :style="{ color: item.iconColor }"
          ></span>
          <span class="title_second">{{ item.title }}</span>
        </div>
      </div>
    </div>
    <!-- <Footer/> -->
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
