</template>
<script>
// @ is an alias to /src
import Header from "./components/Header";
import Footer from "./components/Footer";
import { list } from "../../src/config/index";
export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  data() {
    return {
      list_first: [],
      list_second: [],
    };
  },
  created() {
    this.list_first = list.filter((s) => s.desktop);
    this.list_second = list.filter((s) => !s.desktop);
  },
  methods: {
    skipto(item) {
      this.$router.push({
        path: item.path,
        query: { item: JSON.stringify(item) },
      });
    },
  },
};
</script>
