<template>
  <div class="header">
    <div class="posi">
      <div class="waiting" v-if="!city">
        <mt-spinner type="fading-circle" color="#fff"></mt-spinner>
      </div>
      <div v-else>
        <div class="position">
          {{ city }}&nbsp;<span class="font ixia"></span>
        </div>
        <p class="temp">{{ wea }}&nbsp;{{ tem }}℃</p>
      </div>
    </div>
    <div class="search">
      <div class="searchbox">
        <div class="searchicon">
          <span class="font isousuo"></span>
        </div>
        <div class="searchinp">
          <input
            type="text"
            v-model="searchData"
            class="inp"
            @input="inputchange"
            @focus="hide"
            @blur="hide"
          />
        </div>
        <div class="searchbtn" @click="clean">清空</div>
        <div class="resultbox" v-show="result.length">
          <p
            v-for="(item, index) in result"
            :key="index"
            class="item"
            @click="skipto(item)"
          >
            {{ item.title }}
          </p>
        </div>
      </div>
    </div>
    <div class="add">
      <span class="font itianjia" @click="isshow"></span>
      <mt-popup
        v-model="popupVisible"
        :modal="false"
        position="top"
        popup-transition="popup-fade"
      >
        <div class="addbox">
          <p class="addselect" v-for="(item,index) in list" :key="index" @click="add(item)">
            <span class="font" :class="item.icon"></span>
            <span class="title">{{item.title}}</span>
          </p>
        </div>
        <p class="sanjiao"></p>
      </mt-popup>
    </div>
  </div>
</template>
<script>
import { list } from "../../config/index";

export default {
  data() {
    return {
      city: "",
      wea: "",
      tem: "",
      searchData: "",
      result: [],
      list: [],
      popupVisible: false,
      all: [],
    };
  },
  created() {
    this.all=list
    this.list = list.filter(s=>s.addabled);
  },
  mounted() {
    this.get(
      "https://v0.yiketianqi.com/api?version=v61&appid=52152633&appsecret=a1G14628"
    ).then((data) => {
      console.log(data);
      this.city =
        data.city.length > 2 ? data.city.substring(0, 2) + "..." : data.city;
      this.wea = data.wea;
      this.tem = data.tem;
    });
  },
  methods: {
    innerChange(value) {
      this.result = [];
      if (!value) {
        return;
      }
      if (this.all.some((ele) => ele.title.indexOf(value) != -1)) {
        this.result = this.all.filter((e) => e.title.indexOf(value) != -1);
        console.log(this.result);
      }
    },
    inputchange(e) {
      this.searchData = e.target.value;
      let value = e.target.value;
      this.innerChange(value);
    },
    clean() {
      this.hide()
      this.searchData = "";
      this.innerChange("");
    },
    skipto(item) {
      this.hide()
      this.$router.push({
        path: item.path,
        query: { item: JSON.stringify(item) },
      });
    },
    isshow() {
      this.popupVisible = !this.popupVisible;
    },
    add(item){
      this.$router.push({
        path: item.path,
        query: { item: JSON.stringify(item) },
      });
    },
    hide(){
      this.popupVisible = false;
    }
  },
};
</script>
<style lang="less" scoped>
.font {
  color: @white;
  font-size: 32px;
}
.isousuo {
  color: @info;
  font-size: 34px;
}
.header {
  height: 100px;
  background: @color;
  position: fixed;
  top: 50px;
  left: 0;
  right: 0;
  display: flex;
  font-size: 28px;
  z-index: 1000;
  .posi {
    width: 150px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .waiting {
      width: 56px;
      height: 56px;
      margin: 0 auto;
    }
    .position {
      height: 50px;
      line-height: 50px;
      text-align: center;
      color: @white;
      font-size: 28px;
    }
    .temp {
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 24px;
      color: @white;
    }
  }
  .search {
    width: 500px;
    padding: 18px 6px;
    .searchbox {
      height: 64px;
      display: flex;
      background: @white;
      border-radius: 10px;
      position: relative;
      .searchicon {
        text-align: center;
        line-height: 64px;
        flex: 1;
        color: @color;
      }
      .searchinp {
        flex: 6;
        .inp {
          font-size: 28px;
          width: 100%;
          height: 100%;
          border: none;
          outline: none;
          color: @info;
        }
      }
      .searchbtn {
        height: 52px;
        text-align: center;
        line-height: 52px;
        margin: 6px;
        border-radius: 6px;
        background: @background;
        flex: 2;
        font-size: 26px;
      }
    }
    .resultbox {
      position: absolute;
      left: 0;
      top: 64px;
      right: 0;
      background: @white;
      border-radius: 10px;
      padding: 20px 0;
      .item {
        color: @color;
        font-size: 28px;
        padding: 15px 0;
        text-indent: 20px;
      }
    }
  }
  .add {
    width: 80px;
    line-height: 100px;
    text-align: center;
    transform: scale(1.2);
    position: relative;
    .addbox{
      width:160px;
      position: absolute;
      top:85px;
      right: -20px;
      background: @dark;
      color: @white;
      border-radius: 10px;
      padding:10px;
      .addselect{
        height: 30px;
        line-height: 30px;
        padding:10px 20px;
        padding-right: 0;
        text-align: left;
        .title,.font{
          font-size:24px;
        }
        .font{
          margin-right:10px;
        }
      }
    }
    .sanjiao{
      width: 0;
      height: 0;
      border-left:8px solid transparent;
      border-right: 8px solid transparent;
      border-bottom: 12px solid @dark;
      position: absolute;
      top:74px;
      right: -8px;
      z-index: 1001;
    }
  }
}
</style>
